import React, { Component } from 'react';
import './App.css';
import { TwitterPicker } from 'react-color';

class App extends Component {

  constructor() {
    super();
    this.state = { pixels: [], currentPixel: null, backgroundColor: "#fafafa", pixelSize: 25, highlightedPixel: null };
    this.pixelClicked = this.pixelClicked.bind(this);
    this.coordinateClicked = this.coordinateClicked.bind(this);
    this.onColorChanged = this.onColorChanged.bind(this);
    this.updateColor = this.updateColor.bind(this);
    this.switchBackground = this.switchBackground.bind(this);
    this.removePixel = this.removePixel.bind(this);
    this.deletePixel = this.deletePixel.bind(this);
    this.highlightPixel = this.highlightPixel.bind(this);
  }

  pixelClicked(event) {
    let currPixelClicked = { x: Math.floor(event.clientX/this.state.pixelSize), y: Math.floor(event.clientY/this.state.pixelSize) }
    this.coordinateClicked({ id: `${currPixelClicked.x}-${currPixelClicked.y}`, x: currPixelClicked.x, y: currPixelClicked.y })
  }

  deletePixel(event) {
    event.preventDefault();
    let currPixelClicked = { x: Math.floor(event.clientX/this.state.pixelSize), y: Math.floor(event.clientY/this.state.pixelSize) };
    this.removePixel(currPixelClicked);
    this.setState({ pixels: this.state.pixels });
  }

  removePixel({x, y}) {
    for (let i=0; i<this.state.pixels.length; i++){
      if(this.state.pixels[i].id === `${x}-${y}`){
        this.state.pixels.splice(i,1)
      }
    }
  }

  coordinateClicked({ id, x, y }) {
    this.setState({ currentPixel: { id, x, y } })
  }

  onColorChanged(event) {
    this.removePixel({x: this.state.currentPixel.x, y: this.state.currentPixel.y});
    this.setState({
      pixels: this.state.pixels.concat({
        ...this.state.currentPixel, color: event.hex
      }),
      backgroundColor: this.state.backgroundColor
    })
  }

  updateColor(id, x, z) {
    console.log(`${id} ${x} ${z}`);
  }

  highlightPixel(event){
    let currPixelClicked = { x: Math.floor(event.clientX/this.state.pixelSize), y: Math.floor(event.clientY/this.state.pixelSize) }
    this.setState({ highlightedPixel: { x: currPixelClicked.x, y: currPixelClicked.y } })
  }

  switchBackground(){
    let bgColor = null;
    if(this.state.backgroundColor === "#fafafa"){
      bgColor = "#1b1b1b";
    }
    else {
      bgColor = "#fafafa";
    }
    this.setState({ backgroundColor: bgColor })
  }

  render() {
    return (
      <div style={{ backgroundColor: this.state.backgroundColor }} className="main-app">
        <nav className="navbar navbar-expand-lg">
          <button className="navbar-toggler" type="button" data-toggle="collapse" data-target="#navbarTogglerDemo01" aria-controls="navbarTogglerDemo01" aria-expanded="false" aria-label="Toggle navigation">
            <span className="navbar-toggler-icon"></span>
          </button>
          <div className="collapse navbar-collapse" id="navbarTogglerDemo01">
            <h1 className="text-center margin-0 text-rot-color"><i style={{ transform: "scale(0.8)" }} className="fas fa-palette"></i> Color Pixel</h1>
            <div className="ml-auto">
              <button className="btn btn-secondary border-color" type="button" onClick={this.switchBackground}>Toggle Background</button>&nbsp;&nbsp;&nbsp;
              <button type="button" className="btn color-button" data-toggle="modal" data-target="#exampleModal"><i className="fas fa-question text-black-50"></i></button>
            </div>
            
            {/* <i className="ml-auto">&times;</i> */}
          </div>
        </nav>
        { this.state.currentPixel && <div style={{ top: (this.state.currentPixel.y * this.state.pixelSize) + this.state.pixelSize*1.5 , left:this.state.currentPixel.x * this.state.pixelSize, position:'absolute' }}><TwitterPicker onChange={this.onColorChanged}/><button className="btn" style={{ display: "flex", float: "right" }} onClick={()=> {this.setState({ currentPixel: null })}}>&times;</button></div> }
        { this.state.highlightedPixel && <div style={{ top: (this.state.highlightedPixel.y * this.state.pixelSize) , left:this.state.highlightedPixel.x * this.state.pixelSize, position:'absolute', height: this.state.pixelSize, width: this.state.pixelSize, border: '0.1rem solid cyan', boxShadow: "0.5px 0.5px 10px 1px cyan" }}></div>}
        
        <main className="mt-3">
          <section onMouseMove={this.highlightPixel} onClick={this.pixelClicked} onMouseLeave={()=> { this.setState({ highlightedPixel: null }) }} style={{ maxWidth: '100%', height: '90vh' }}>
            {this.state.pixels.map(({ id, x , y, color}) =>
              <div key={id} onContextMenu={this.deletePixel} style={{
                backgroundColor: color,
                top: y * this.state.pixelSize,
                left: x * this.state.pixelSize,
                width: `${this.state.pixelSize}px`,
                height: `${this.state.pixelSize}px`,
                position: `absolute`
              }}></div>
            )}
          </section>
        </main>

        <footer>
          <p className="margin-0 text-center pad-0-5 text-rot-color">Made with <i style={{ color: "red" }} className="fas fa-heart"></i> using <i style={{ color: "#61dafb" }} className="fab fa-react"></i></p>
        </footer>

        <aside>
        <div className="modal fade" id="exampleModal" tabindex="-1" role="dialog" aria-labelledby="exampleModalLabel" aria-hidden="true">
          <div className="modal-dialog" role="document">
            <div className="modal-content">
              <div className="modal-header">
                <h5 className="modal-title" id="exampleModalLabel">Instructions</h5>
              </div>
              <div className="modal-body">
                <div className="container-fluid">
                <ul>
                  <li><b>Add Pixel</b> : Left Click</li>
                  <li><b>Delete Pixel</b> : Right Click</li>
                  <li><b>Change Color</b> : Click on pixel and choose the color (you can even input a hex value)</li>
                  <li><b>Remove Color Picker Panel</b> : Click on cross button on bottom of Picker</li>
                </ul>
                </div>
              </div>
              <div className="modal-footer">
                <button type="button" className="btn btn-secondary" data-dismiss="modal">Okay</button>
              </div>
            </div>
          </div>
        </div>  
        </aside>        
      </div>
    );
  }
}

export default App;


